	data() {
		return {
			// tabBarId: 3,
			navIndex: -1, // 判断右边导航栏动画移动的高度
			userIconPop: false,
			showComponente: [
				//  判断 显示 那一个  组件页面
				{
					isShow: false,
					id: 0
				},
				{
					isShow: false,
					id: 1
				},
				{
					isShow: false,
					id: 2
				},
				{
					isShow: false,
					id: 3
				},
				{
					isShow: false,
					id: 4
				}
			],
			userIcon: '',
		};
	},
	methods: {
		getUpx(index) {
			// 点击  显示 相对应的组件以及右边导航栏的动画移动位置
			this.userIconPop = false;
			this.navIndex = index;
			this.showComponente.forEach((v, i) => {
				v.isShow = false;
				if (v.id == index) {
					v.isShow = true;
				}
			});
		},
		makingACall() {
			//点击客服  拨打电话
			uni.makePhoneCall({
				phoneNumber: '114' //仅为示例
			});
		},
		hideUserIcon(idx) {
			this.navIndex = idx;
			this.showComponente.forEach((v, i) => {
				v.isShow = false;
				// 				if (v.id == index) {
				// 					v.isShow = true;
				// 				}
			});
		},
		userIconleftShift() {
			this.userIconPop = true;
		},
	},
	onLoad() {
		this.userIconPop = true;
		
		const userInfo = uni.getStorageSync('userInfo');

		if (userInfo.avatarUrl) {
			this.userIcon = userInfo.avatarUrl;
		}
	}
};
